<template>
  <div class="content" v-if="visible">
    <div class="content p-3 bg-dark rounded-3">
      <header class="text-center text-light">Wolfex | Jobcenter</header>
      <div class="list mt-1" v-for="job in jobs" :key="job">
        <ul class="list-group list-group-vertical text-center">
          <li @click="() => updateJob(job)" class="list-group-item bg-primary mt-1 text-light">
            {{ job.toUpperCase() }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'JobVUE',
  data() {
    return {
      jobs: ['police', 'medic', 'taxi', 'miner'],
      visible: false,
      eventListener: ({ data }) => {
        this.jobs = data.jobs;
        if (data.type === 'job') {
          this.visible = !this.visible;
        }
      },
    };
  },
  methods: {
    updateJob(job) {
      fetch('https://wolfex_jobs/setJob', { method: 'POST', body: JSON.stringify({ job: job }) });
    },
  },
  mounted() {
    window.addEventListener('message', this.eventListener);
  },
  unmounted() {
    window.addEventListener('message', this.eventListener);
  },
};
</script>

<style></style>
